import { addFilter } from '@wordpress/hooks';
import { BaseControl, Notice, SelectControl, ToggleControl, TextControl, TextareaControl } from '@wordpress/components'

const { createHigherOrderComponent } = wp.compose;
const { InspectorControls } = wp.blockEditor;
const { PanelBody, PanelRow } = wp.components;

function addCPCMUnfoldAttribute(settings) {
	if (typeof settings.attributes !== 'undefined') {
		if (settings.name == 'core/navigation-link') {
			settings.attributes = Object.assign(settings.attributes, {
				'cpcm_unfold': {
					type: 'boolean',
				},
				'cpcm_item_count': {
					type: 'integer',
				},
				'cpcm_orderby': {
					type: 'string',
				},
				'cpcm_item_titles': {
					type: 'string',
				}
			});
		}
	}
	return settings;
}

addFilter(
	'blocks.registerBlockType',
	'cpcm/unfold',
	addCPCMUnfoldAttribute
);

const cpcmUnfoldControls = createHigherOrderComponent((BlockEdit) => {
	return (props) => {
		return (
			<>
				<BlockEdit key="edit" {...props} />
				{props.attributes.kind === 'taxonomy'
					? <InspectorControls>
						<PanelBody title="Category Posts in Custom Menu" initialOpen={false}>
							<BaseControl>
								<ToggleControl
									id='cpcm_unfold'
									key='cpcm_unfold'
									label={wp.i18n.__('Create submenu containing posts' + (props.attributes.type === 'category' ? ' in this category.' : props.attributes.type === 'tag' ? ' with this tag.' : ' in this taxonomy.'), 'category-posts-in-custom-menu')}
									checked={!!props.attributes.cpcm_unfold}
									onChange={(newValue) => props.setAttributes({ cpcm_unfold: newValue })}
								/>
							</BaseControl>
							{props.attributes.cpcm_unfold
								? <>
									<BaseControl>
										<TextControl
											label={<>
												{wp.i18n.__('Number of posts', 'category-posts-in-custom-menu')} (max 10)
											</>}
											value={props.attributes.cpcm_item_count > 0 ? props.attributes.cpcm_item_count : 10}
											onChange={(newValue) => props.setAttributes({ cpcm_item_count: isNaN(+newValue) || (+newValue) > 10 ? 10 : +newValue })}
										/>
									</BaseControl>
									<BaseControl>
										<SelectControl
											label={wp.i18n.__('Order by', 'category-posts-in-custom-menu')}
											value={props.attributes.cpcm_orderby ?? 'none'}
											options={[
												{ label: wp.i18n.__('None', 'category-posts-in-custom-menu'), value: 'none' },
												{ label: wp.i18n.__('Title', 'category-posts-in-custom-menu'), value: 'title' },
												{ label: wp.i18n.__('Date', 'category-posts-in-custom-menu'), value: 'date' },
												{ label: wp.i18n.__('Menu Order', 'category-posts-in-custom-menu'), value: 'menu_order' }
											]}
											onChange={(newValue) => props.setAttributes({ cpcm_orderby: newValue })}
										/>
									</BaseControl>
									<BaseControl>
										<TextareaControl
											label={<>
												{wp.i18n.__('Navigation Label', 'category-posts-in-custom-menu')}
											</>}
											value={props.attributes.cpcm_item_titles ?? '%post_title'}
											onChange={(newValue) => props.setAttributes({ cpcm_item_titles: newValue })}
										/>
									</BaseControl>
									<BaseControl>
										{wp.i18n.__('The navigation label may be customized using wildcards such as %post_title. See documentation.', 'category-posts-in-custom-menu')}
									</BaseControl>
								</>
								: null}
						</PanelBody>
					</InspectorControls>
					: null}
			</>
		);
	};
}, 'withMyPluginControls');

wp.hooks.addFilter(
	'editor.BlockEdit',
	'cpcm/unfold',
	cpcmUnfoldControls
);
